body{
    background-color: #fff;
}
.todo-container{
    width: 600px;
    margin: 0 auto;
    padding: 10px; 
    border: solid 1px #ddd;
    border-radius: 5px;

}





  .btn {
    /* display: none; */
    padding: 4px 12px;
    margin-top: 3px;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
  }
  
  .btn-danger {
    color: #fff;
    background-color: #da4f49;
    border: 1px solid #bd362f;
  }
  
  .btn-danger:hover {
    color: #fff;
    background-color: #bd362f;
  }
  
  .btn:focus {
    outline: none;
  }